//
// Gradient Bar
//
// Apply this class to any block HTML element
// --------------------------------------------------

$stops: ();
@each $stop, $color in $gradient-colors {
  $new-stops: $color percentage($stop / 100);
  $stops: append($stops, $new-stops, comma);
}

.gradient-bar {
  border-top: 5px solid transparent;
  border-image: linear-gradient(to right, $stops);
  border-image-slice: 1;
}



