//
// Color Utility Classes
// --------------------------------------------------
// stylelint-disable block-opening-brace-space-after, block-opening-brace-space-before

// Background Colors
// -------------------------
.bg-blue            { background-color: $blue; }
.bg-purple          { background-color: $purple; }
.bg-yellow          { background-color: $yellow; }
.bg-black           { background-color: $black; }
.bg-white           { background-color: $white; }
.bg-grey            { background-color: $grey; }
.bg-grey-light      { background-color: $grey-light; }
.bg-grey-dark       { background-color: $grey-dark; }
.bg-red             { background-color: $red; }
.bg-orange          { background-color: $orange; }
.bg-green           { background-color: $green; }

.bg-facebook        { background-color: $facebook; }
.bg-twitter         { background-color: $twitter; }
.bg-youtube         { background-color: $youtube; }
.bg-instagram       { background-color: $instagram; }

// Text Colors
// -------------------------
.text-blue          { color: $blue; }
.text-purple        { color: $purple; }
.text-yellow        { color: $yellow; }
.text-black         { color: $black; }
.text-white         { color: $white; }
.text-grey          { color: $grey; }
.text-grey-light    { color: $grey-light; }
.text-grey-dark     { color: $grey-dark; }
.text-red           { color: $red; }
.text-orange        { color: $orange; }
.text-green         { color: $green; }

.link-blue {
  color: $blue;

  &:hover {
    color: darken($blue, 20%);
  }
}

.link-grey {
  color: $grey;

  &:hover {
    color: $blue;
  }
}

.link-grey-dark {
  color: $grey-dark;

  &:hover {
    color: $blue;
  }
}



