//
// Buttons
// --------------------------------------------------

.btn {
  display: inline-block;
  color: $black;
  padding: 10px 20px;
  font-size: $body-font-size;
  font-weight: $font-weight-bold;
  font-family: $font-family-heading;
  cursor: pointer;
  line-height: 1;
  border-radius: $border-radius;
  appearance: none; // Corrects inability to style clickable `input` types in iOS.
  background-color: transparent;
  transition: background-color .1s ease 0s;
  border: 1px solid transparent;
  vertical-align: middle;
  text-align: center;
  user-select: none;
}

.btn-primary { @include btn-solid($white, $purple); }
.btn-secondary { @include btn-solid($black, $grey-light); }
.btn-success { @include btn-solid($black, $yellow); }

.btn-outline-primary { @include btn-outline($black, $yellow); }
.btn-outline-secondary { @include btn-outline($black, $grey-dark); }
.btn-outline-success { @include btn-outline($black, $yellow); }



