//
// Spacer Utilities
// --------------------------------------------------
// stylelint-disable block-opening-brace-space-before

// Margin spacer utilities
// -------------------------
// Loop through the breakpoint values
@each $breakpoint, $variant in $responsive-variants {
  @include breakpoint($breakpoint) {
    // Loop through the spacer values
    @for $i from 1 through length($spacers) {
      $size: nth($spacers, $i); // sm, md, lg, xl
      $scale: $i - 1;  // 0, 1, 2, 3, 4, 5, 6

      // Set a $size margin to all sides at $breakpoint
      .m#{$variant}-#{$scale}  { margin: $size; }
      // Set a $size margin on the top at $breakpoint
      .mt#{$variant}-#{$scale} { margin-top: $size; }
      // Set a $size margin on the right at $breakpoint
      .mr#{$variant}-#{$scale} { margin-right: $size; }
      // Set a $size margin on the bottom at $breakpoint
      .mb#{$variant}-#{$scale} { margin-bottom: $size; }
      // Set a $size margin on the left at $breakpoint
      .ml#{$variant}-#{$scale} { margin-left: $size; }

      // Set a $size margin on the left & right at $breakpoint
      .mx#{$variant}-#{$scale} {
        margin-right: $size;
        margin-left: $size;
      }

      // Set a $size margin on the top & bottom at $breakpoint
      .my#{$variant}-#{$scale} {
        margin-top: $size;
        margin-bottom: $size;
      }
    }

    /* responsive horizontal auto margins */
    .mx#{$variant}-auto {
      margin-right: auto;
      margin-left: auto;
    }
  }
}

//
// Padding spacer utilities
// --------------------------------------------------
// Loop through the breakpoint values
@each $breakpoint, $variant in $responsive-variants {
  @include breakpoint($breakpoint) {
    // Loop through the spacer values
    @for $i from 1 through length($spacers) {
      $size: nth($spacers, $i); // sm, md, lg, xl
      $scale: $i - 1;  // 0, 1, 2, 3, 4, 5, 6

      // Set a $size padding to all sides at $breakpoint
      .p#{$variant}-#{$scale}  { padding: $size; }
      // Set a $size padding on the top at $breakpoint
      .pt#{$variant}-#{$scale} { padding-top: $size; }
      // Set a $size padding on the right at $breakpoint
      .pr#{$variant}-#{$scale} { padding-right: $size; }
      // Set a $size padding on the bottom at $breakpoint
      .pb#{$variant}-#{$scale} { padding-bottom: $size; }
      // Set a $size padding on the left at $breakpoint
      .pl#{$variant}-#{$scale} { padding-left: $size; }

      // Set a $size padding on the left & right at $breakpoint
      .px#{$variant}-#{$scale} {
        padding-right: $size;
        padding-left: $size;
      }

      // Set a $size padding on the top & bottom at $breakpoint
      .py#{$variant}-#{$scale} {
        padding-top: $size;
        padding-bottom: $size;
      }
    }

    /* responsive horizontal auto paddings */
    .px#{$variant}-auto {
      padding-right: auto;
      padding-left: auto;
    }
  }
}



