//
// Buttons Mixins
// --------------------------------------------------

// Button Mixins Generators
// -------------------------
@mixin btn-solid($text-color, $bg-color) {
  color: $text-color;
  background-color: $bg-color;

  &:hover {
    background-color: darken($bg-color, 3%);
  }
}

// If the text color is unreadble on `:hover`, you can add a hovered text color!
@mixin btn-outline($text-color, $border-color, $text-color-hoverd: "inherit") {
  color: $text-color;
  border-color: $border-color;
  background-color: transparent;

  &:hover {
    background-color: $border-color;
    color: $text-color-hoverd;
  }
}



