//
// Layout Utilities
// --------------------------------------------------
// stylelint-disable block-opening-brace-space-after, block-opening-brace-space-before

// Clear floats
// Clear floats around the element
// ---------------------
.clearfix { @include clearfix; }

// Positions
// ---------------------
.p-static   { position: static; }
.p-relative { position: relative; }
.p-absolute { position: absolute; }
.p-fixed    { position: fixed; }

.top-0    { top: 0; }
.right-0  { right: 0; }
.bottom-0 { bottom: 0; }
.left-0   { left: 0; }

// Displays
// ---------------------
$display-values: (
  block,
  flex,
  inline,
  inline-block,
  inline-flex,
  none,
  table,
  table-cell
);

// Responsive display utilities
@each $breakpoint, $variant in $responsive-variants {
  @include breakpoint($breakpoint) {
    @each $display in $display-values {
      .d#{$variant}-#{$display} { display: $display; }
    }
  }
}

// Visibility
// ---------------------
// Only display content to screen readers
// See: http://a11yproject.com/posts/how-to-hide-content/
.sr-only { @include visually-hidden; }

.v-hidden { visibility: hidden; }
.v-visible { visibility: visible; }

// Floats
// ---------------------
@each $breakpoint, $variant in $responsive-variants {
  @include breakpoint($breakpoint) {
    .float#{$variant}-left  { float: left; }
    .float#{$variant}-right  { float: right; }
    .float#{$variant}-none { float: none; }
  }
}

// Widths
// Width and height utilities, helpful in combination
// with display-table utilities and images
// ---------------------
.width-fit   { max-width: 100%; }
.width-full  { width: 100%; }
.height-fit  { max-height: 100%; }
.height-full { height: 100%; }
.min-width-0 { min-width: 0; }

// Vertical Align
// -------------------------
.v-align-middle      { vertical-align: middle; }
.v-align-top         { vertical-align: top; }
.v-align-bottom      { vertical-align: bottom; }
.v-align-text-top    { vertical-align: text-top; }
.v-align-text-bottom { vertical-align: text-bottom; }
.v-align-baseline    { vertical-align: baseline; }



