//
// Typography Utility Classes
// --------------------------------------------------
// stylelint-disable selector-list-comma-newline-after, selector-max-type

// Typography Emphasis and Styling
// -------------------------
.text-normal { font-weight: $font-weight-normal; }
.text-bold { font-weight: $font-weight-bold; }
.text-italic { font-style: italic; }
.text-lowercase { text-transform: lowercase; }
.text-uppercase { text-transform: uppercase; }
.text-capitalize { text-transform: capitalize; }
.text-underline { text-decoration: underline; }
.no-underline { text-decoration: none; }
.text-nowrap { white-space: nowrap; }
.text-truncate { @include text-truncate; }

// Text Alignment
// -------------------------
.text-left { text-align: left; }
.text-center { text-align: center; }
.text-right { text-align: right; }
.text-justify { text-align: justify; }

// List styles
// -------------------------
.list-unstyled { @include list-unstyled; }

.list-separated {
  @include list-unstyled;

  li {
    padding-right: .5rem;
    margin-right: .5rem;
    display: inline-block;
    border-right: 1px solid $grey-dark;
    line-height: 1;
    margin-bottom: 5px;

    &:last-child {
      padding-right: 0;
      margin-right: 0;
      border-right: 0;
    }
  }
}

// Heading utilities
// -------------------------
.h1, .h2, .h3, .h4, .h5, .h6 { font-weight: $font-weight-bold; }

.h1 { @include h1; }
.h2 { @include h2; }
.h3 { @include h3; }
.h4 { @include h4; }
.h5 { @include h5; }
.h6 { @include h6; }

.text-small { font-size: $h6-font-size; }

.link-underlined { text-decoration: underline; }



