//
// Text Mixins
// --------------------------------------------------

// Text truncate
// Requires inline-block or block for proper styling
// ---------------------
@mixin text-truncate {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

// Unstyled keeps list items block level,
// just removes default browser padding, margin and list-style
// ---------------------
@mixin list-unstyled {
  padding-left: 0;
  margin-left: 0;
  list-style: none;
}

// Headings Mixins
// ---------------------
@mixin h1 {
  font-size: $h1-font-size-mobile;
  font-weight: $font-weight-bold;
  line-height: $h1-line-height-mobile;

  @include breakpoint(md) {
    font-size: $h1-font-size;
    line-height: $h1-line-height;
  }
}

@mixin h2 {
  font-size: $h2-font-size-mobile;
  font-weight: $font-weight-bold;
  line-height: $h2-line-height-mobile;

  @include breakpoint(md) {
    font-size: $h2-font-size;
    line-height: $h2-line-height;
  }
}

@mixin h3 {
  font-size: $h3-font-size-mobile;
  font-weight: $font-weight-bold;
  line-height: $h3-line-height-mobile;

  @include breakpoint(md) {
    font-size: $h3-font-size;
    line-height: $h3-line-height;
  }
}

@mixin h4 {
  font-size: $h4-font-size-mobile;
  font-weight: $font-weight-bold;
  line-height: $h4-line-height-mobile;

  @include breakpoint(md) {
    font-size: $h4-font-size;
    line-height: $h4-line-height;
  }
}

@mixin h5 {
  font-size: $h5-font-size;
  font-weight: $font-weight-bold;
  line-height: $h5-line-height;
}

@mixin h6 {
  font-size: $h6-font-size;
  font-weight: $font-weight-bold;
  line-height: $h6-line-height;
}



